
import { mapState } from 'vuex'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Box from 'vux/src/components/box/index.vue'

export default {
directives: {
},
components: {
Qrcode,
FlexboxItem,
Flexbox,
Box
},
name: 'myvcard',
data () {
return {
out_trade_no: '',
use_record_list: []
}
},
computed: {
},
created: function() {
this.out_trade_no = this.$route.query.out_trade_no
this.fetchData()
},
activated: function(){

},
deactivated: function(){
},
methods: {
fetchData: function(){
let self = this
self.xarpost('Vcard/getMyVcard', {out_trade_no: this.out_trade_no}).then( d => {
let {data, error, message} = d
if(error == 0){
self.use_record_list = data
console.log(data)
}
})
}
}
}



